import { useDispatch, useSelector } from "react-redux";
import React, { useEffect } from "react";
import { getReply, addReply } from "../../redux/modules/Reply";
import ReplyItem from "./ReplyItem";
import useInput from "../../hooks/useInput";
import styled, { ThemeProvider } from "styled-components";
import Button from "../button/Button";

function ReplyList() {
  const [userValue, setUserValue] = useInput();
  const [commentValue, setCommentValue] = useInput();
  //     state
  const replyList = useSelector((state) => state.replyReducer);
  //     hook

  const dispatch = useDispatch();
  //    hook

  useEffect(() => {
    dispatch(getReply());
  }, []);
  //useEffect  useEffect      .
  //   
  // input   .

  // []     []  state   state   .
  //[]   useeffect    .

  const ReplyAdd = (e) => {
    e.preventDefault();

    if (userValue) {
      const newList = { username: userValue, comment: commentValue };
      //   input 2  
      dispatch(addReply(newList));
      setUserValue("");
      setCommentValue("");
    } else {
      alert("");
    }
  };

  function handleInputLength(el, max) {
    if (el.value.length > max) {
      el.value = el.value.substr(0, max);
    }
  }

  return (
    <Alcoform>
      <form action="" onSubmit={ReplyAdd}>
        <FormContent>
          <Label></Label>
          <NicInput
            type="text"
            onChange={(e) => setUserValue(e.target.value)}
            value={userValue}
            id="user"
            maxLength="6"
            placeholder=" ."
          />
        </FormContent>
        <FormContent>
          <Label></Label>
          <CommentInput
            type="text"
            onChange={(e) => setCommentValue(e.target.value)}
            value={commentValue}
            id="comment"
            maxLength="20"
            placeholder="20    ."
          />
        </FormContent>
        <ThemeProvider
          theme={{
            palette: {
              green: "#9BFFDA",
              purple: "#FF9DFF",
              red: "#FF4646",
            },
          }}
        >
          <Button color="green" type="submit" style={{ margin: "0 0 0 8px" }}>
            
          </Button>
        </ThemeProvider>
      </form>
      <ul>
        {replyList.map((data) => (
          <ReplyItem
            key={data.id}
            replyId={data.id}
            username={data.username}
            comment={data.comment}
          ></ReplyItem>
        ))}
      </ul>
    </Alcoform>
  ); //return
} //ReplyList

export default ReplyList;

const Alcoform = styled.div`
  width: 600px;
  height: 220px;
  border-bottom: 1px solid black;
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 100px;
`;
const FormContent = styled.div`
  display: inline-flex;
  justify-content: center;
  align-items: center;
  margin: 0.5rem 0;
`;
const Label = styled.label`
  border-radius: 20px;
  font-weight: bold;
  color: #3cfbff;
  margin: 0 0.3rem 0 0;
`;
const NicInput = styled.input`
  width: 200px;
  height: 35px;
  border-radius: 1rem;
  padding: 0.3rem;
  color: black;
`;

const CommentInput = styled.input`
  width: 481px;
  height: 35px;
  border-radius: 20px;
  padding: 0.3rem;
  color: black;
`;
